import { useState } from 'react';
import 'bulma/css/bulma.css';
import './App.scss';

export const goods = [
  'Dumplings',
  'Carrot',
  'Eggs',
  'Ice cream',
  'Apple',
  'Bread',
  'Fish',
  'Honey',
  'Jam',
  'Garlic',
];

type Good = string | null;

export const App: React.FC = () => {
  const [selectedGood, setSelectedGood] = useState<Good>('Jam');

  const clearSelection = () => {
    setSelectedGood(null);
  };

  return (
    <main className="section container">
      {!selectedGood
        ? <h1 className="title">No goods selected</h1>
        : (
          <h1 className="title is-flex is-align-items-center">
            {`${selectedGood} is selected`}

            {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
            <button
              data-cy="ClearButton"
              type="button"
              className="delete ml-3"
              aria-label="Clear selected good"
              onClick={clearSelection}
            />
          </h1>
        )}

      <table className="table">
        <tbody>
          {goods.map(good => (
            <tr
              data-cy="Good"
              className={`${selectedGood === good ? 'has-background-success-light' : ''}`}
              key={good}
            >
              <td>
                <button
                  data-cy={selectedGood === good ? 'RemoveButton' : 'AddButton'}
                  type="button"
                  className={selectedGood === good
                    ? 'button is-info' : 'button'}
                  onClick={() => setSelectedGood(selectedGood === good
                    ? null : good)}
                >
                  {selectedGood === good ? '-' : '+'}
                </button>
              </td>
              <td data-cy="GoodTitle" className="is-vcentered">
                {good}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </main>
  );
};
